import React from 'react';
import Profesor from './Profesor';
import DataProfes from '../dataJSON/profesores.json'

const PROFES = DataProfes.profesJSON;

export default class Profesores extends React.Component {
    render() {
        return (
            <div id="Profesores">
                <div className="container">
                    <h2 className="sub-titulo">Plana docente</h2>
                    {PROFES.map(item => <Profesor key={item.id} data={item} /> )}
                </div>
            </div>
        )
    }
}