import React from 'react';
import './styles/Profesor.scss';

function Profesor({data}) {
    const {name, cover, text, facebook, curso} = data;
    return (
        <article className="Profesor">
            <figure className="Profesor_image_Container">
              <img className="Profesor_image" src={cover} alt={name} width="250px" height="250px" />
            </figure>
            <div className="Profesor_details">
              <h3 className="Profesor_title">{name}</h3>
              <p className="Profesor_curso"><strong>{curso}</strong></p>
              <p className="Profesor_mensaje">{text}</p>
              <p className="Profesor_url"><small><strong>Facebook:</strong> <a href={`https://www.facebook.com/${facebook}`}>{name}</a></small></p>
            </div>
        </article>
    )
}

export default Profesor;