import React from 'react';
import {BrowserRouter, Switch, Route} from 'react-router-dom';

import Home from './pages/Home';
import GradoPageContainer from './pages/GradoPageContainer';
import Layout from './components/Layout';
import NotFound from './pages/NotFound';

function App() {
  return (
    <BrowserRouter>
      <Layout logo="https://i.imgur.com/lfRaaAE.jpg">
          <Switch>
              <Route exact path="/" component={Home} />
              <Route exact path="/grado/:grID" component={GradoPageContainer} />
              <Route component={NotFound} />
          </Switch>
      </Layout>
  </BrowserRouter>
  );
}

export default App;
