import React from 'react';
import './styles/Pregunta.scss';

function Pregunta({data, grID, isCorrected, upScore}) {
      const {numeroPreg, profesor, curso, texto, alternativas, respuesta, cover} = data;
      const [isOK, setIsOK] = React.useState(false);
      let PreguntaBG, RespuestaBG;
      React.useEffect(() => upScore(isCorrected, isOK), [isCorrected, isOK, upScore]);

      if(isCorrected && isOK) {
        PreguntaBG = {background: "#00ff2b25"};
        RespuestaBG = {background: "#00ff29ab"};
      }
      else if(isCorrected && !isOK) {
        PreguntaBG = {background: "#ff000025"};
        RespuestaBG = {background: "#ff0700ab"};
      }
      else {
        PreguntaBG = {background: "white"};
        RespuestaBG = {background: "white"};
      }
      return (
          <div className="Pregunta" style={PreguntaBG}>
              <h4 className="Pregunta_title">Pregunta {numeroPreg} <small>({curso})</small></h4>
              <p className="Pregunta_parrafo">{texto}</p>
              {cover && <img alt="Pregunta_cover" className="Pregunta_cover" src={cover}/>}

              {!isCorrected && <div className="Pregunta_CajaAlts">
                {alternativas.map((item, index) => 
                  <label className="Pregunta_alternativa" key={index}><input onChange={(elem) => {
                    if(elem.target.checked && item === respuesta) {
                      setIsOK(true)
                    } else {
                      setIsOK(false)
                    }
                  }} name={`${grID}_p${numeroPreg}`} type="radio" /> <span></span>{item}</label>
                )}
              </div>}
              {isCorrected && <div className="Pregunta_Respuesta" style={RespuestaBG}>
                ¡Respuesta {isOK ? "Correcta!" : "Incorrecta!: " } <strong>{respuesta}</strong>
              </div>}
          </div>
    )
}

export default Pregunta;