import React from 'react';
import Grado from './Grado';
import DataGrades from '../dataJSON/grados.json';

const GRADOS = DataGrades.gradosJSON;

export default class Grados extends React.Component {
    render() {
        return (
            <div id="Grados">
                <div className="container">
                    <h2 className="sub-titulo">Grados</h2>
                    {GRADOS.map(item => <Grado key={item.id} data={item} /> )}
                </div>
            </div>
        )
    }
}