import React from 'react';
import './styles/GradoPage.scss';
import Portada from './Portada';
import Pregunta from './Pregunta';

function GradoPage ({data, preguntas, grID, isCorrected, upScore, puntaje, corregir}) {
    return (
        <React.Fragment>
            <Portada ruleta={data.ruleta}/>
            <div className="GradoPage_Details">
                <div className="container">
                    <div className="GradoPage_fila">
                        <h2 className="GradoPage_title">{data.title}</h2>
                        <p className="GradoPage_mensaje">{data.text}</p>
                    </div>
                    <div className="GradoPage_fila">
                        <h3>Cursos de reforzamiento:</h3>
                        {data.cursos.map((item, index) => 
                            <li key={index} className="GradoPage_lista">
                                <strong>{item.name}</strong> <span>({item.hours})</span>
                            </li>
                        )}
                    </div>
                    {/* <div className="GradoPage_fila">
                        <h3>Horario</h3>

                    </div> */}
                    <div className="GradoPage_fila">
                        <h3>Alumnos destacados <span>(ciclo académico 2020)</span>:</h3>
                        {data.alumnosDes.map((item, index) => 
                            <li key={index} className="GradoPage_lista mayus">
                                <strong>{item.puesto}:</strong> {item.nombre}
                            </li>
                        )}
                    </div>
                    <div className="GradoPage_fila">
                        <h3>Ejercicios propuestos</h3>
                        {preguntas.map((item, index) => 
                            <Pregunta key={index} data={item} grID={grID} isCorrected={isCorrected} upScore={upScore}/>
                        )}
                    </div>
                    <div className="GradoPage_fila puntaje">
                        <div className="GradoPage_score">Score: {puntaje}/{preguntas.length}  
                            {puntaje === (preguntas.length) && "  (¡Felicidades!)"}
                            {puntaje === (preguntas.length - 1) && "  (!Bien Hecho!)"}
                        </div>
                        {!isCorrected && 
                            <button className="btn btn-primary" onClick={corregir}>Corregir</button>
                        }
                        
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default GradoPage;