import React from 'react';
import './styles/Mensaje.scss';

function Mensaje(props) {
    const {author, date, title, text} = props.data;
    return (
        <div className="Mensaje">
            <div className="Mensaje_body">
                <h3 className="Mensaje_title">{title}</h3>
                <span className="Mensaje_date">{date}</span>
                <p className="Mensaje_text">{text}</p>
                <span className="Mensaje_author">{author}</span>
            </div>
            
        </div>
    )
}

export default Mensaje;