import React from 'react';

import Modal from './Modal';
import './styles/FormOpinionModal.scss';

export default function FormOpinionModal (props) {
    return (
        <Modal modalIsOpen={props.modalIsOpen} onClose={props.onClose}>
            <div className="FormOpinionModal">
                <div className="row cajita">
                    <h2>Recuerda!</h2>
                    <p>Una vez publicado tu comentario, no podrás editarlo o eliminarlo.</p>
                </div>
                <div className="row">
                    <form  className="FormOpinionModal__Redaccion">
                        <div className="form-group">
                            <label>Nombre:</label>
                            <input  type="text" name="nombre" className="form-control" required maxLength={50}/>
                        </div>
                        <div className="form-group">
                            <label>Calificación:</label>
                            <input  type="text" name="calificacion" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Comentario:</label>
                            <input  type="text" name="comentario" className="form-control" required maxLength={250}/>
                        </div>

                        {props.error && (
                            <p className="text-danger">{props.error.message}</p>
                        )}
                    </form>
                </div>
                <div className="row">
                    <div className="FormOpinionModal__buttonsCaja">
                        <button onClick={props.onClose} className="btn btn-primary">Publicar</button>
                        <button onClick={props.onClose} className="btn btn-danger">Cancelar</button>
                    </div>
                </div>
            </div>
        </Modal>
    )
}