import React from 'react';
import { Link } from 'react-router-dom';
import IamProfessorModal from './IamProfessorModal';
import './styles/NavbarSup.scss';

export default class NavbarSup extends React.Component {
    state = {
        modalIsOpen: false,
    }
    handleModal = () => {
        if(this.state.modalIsOpen) {
            this.setState({modalIsOpen: false})
        } else {
            this.setState({modalIsOpen: true})
        }
    }
    render() {
        return (
            <div className="NavbarSup">
                <div className="container-fluid">
                    <Link to="/" className="NavbarSup__brand">
                        <img className="NavbarSup__brand-logo" src={this.props.logo} alt="Logo"/>
                        <span className="font-weight-light">Academia</span>
                        <span className="font-weight-bold">Millenium</span>
                    </Link>
                    <button className="NavbarSup__logIn" onClick={this.handleModal}>Soy Profesor</button>
                    <IamProfessorModal modalIsOpen={this.state.modalIsOpen} onClose={this.handleModal} />
                </div>
            </div>
        )
    }
}
