import React from 'react';
import './styles/Portada.scss';

function Portada ({ruleta}) {
    const largo = ruleta.length;
    let posicion = 0;
    let posicionCirc = 0;
    
    function moverRuleta(direccion) {
        const $ruleta = document.getElementById('Portada_Ruleta');
        const $circulo = document.getElementById('Portada-puntero');
        switch(direccion) {
            case "<":
                if($ruleta.style.left === '' || $ruleta.style.left === '0%') {
                    posicionCirc = (largo-1)*30;
                    posicion = (largo-1)*(-101.5);
                } else {
                    posicionCirc -=30;
                    posicion+= 101.5;
                }
                break;
            default:
                if($ruleta.style.left === `${(largo-1)*(-101.5)}%`) {
                    posicion = 0;
                    posicionCirc = 0;
                } else {
                    posicion-= 101.5;
                    posicionCirc += 30;
                }
                break;
        }
        $ruleta.style.left = `${posicion}%`;
        $circulo.style.left = `${posicionCirc}px`; 
    }

    return (
        <div className="Portada">
            <div className="Portada_Baner">
                <div className="Portada_Ruleta" id="Portada_Ruleta">
                    {ruleta.map((item, index) => {
                        const position = index*(101.5);
                        return (
                            <div className="Portada_Ruleta-img" key={`Portada_${index}`} style={{left: `${position}%`}}>
                                <img src={item} alt={`Portada_${index}`}/>
                            </div>
                        )
                    })}
                </div>
                <div className="Portada_Posicionamiento">
                    {ruleta.map((item, index) => <div className="Portada_Posicionamiento-item" key={index}></div>)}
                    <div className="Portada_Posicionamiento-item" id="Portada-puntero"></div>
                </div>
                
                {largo>1 && <div className="Portada_Cambio"><button className="Portada_btn" onClick={()=>
                 moverRuleta("<")}>{'<'}</button></div>}
                {largo>1 && <div className="Portada_Cambio der"><button className="Portada_btn" onClick={()=>
                 moverRuleta(">")}>{'>'}</button></div>}
            </div>
        </div>
    )
}
export default Portada;