import React from 'react';
import './styles/NavbarInf.scss';

export default function NavbarInf() {
        return (
            <nav className="NavbarInf">
                <i className="icon-menu burger-button" id="burger-menu" onClick={()=>{
                    const menu = document.getElementById('NavbarInf_Menu');
                    menu.classList.toggle('visible');
                }}></i>
                <ol className="NavbarInf_Menu" id="NavbarInf_Menu">
                    <li className="NavbarInf_Menu-li"><a href="#Grados">Grados</a></li>
                    <li className="NavbarInf_Menu-li"><a href="#Profesores">Profesores</a></li>
                    {/* <li className="NavbarInf_Menu-li"><a href="#Opiniones">Opiniones</a></li> */}
                    <li className="NavbarInf_Menu-li"><a href="#Contacto">Contacto</a></li>
                </ol>
            </nav>
        )
}