import React from 'react';
import Mensaje from './Mensaje'

const mensaje = {
    author: "Gustavo Navarro",
    date: "20/12/2020",
    title: "Sobre el inicio del ciclo académico 2021",
    text: "Estamos a la espera de la orden del gobierno para la reapertura de academias. Estaremos informando tan pronto se emitan los comunicados oficiales."
}

export default class Grados extends React.Component {
    render() {
        return (
            <div id="Mensaje">
                <div className="container">
                    <h2 className="sub-titulo">Mensaje Importante</h2>
                    <Mensaje data={mensaje} />
                </div>
            </div>
        )
    }
}