import React from 'react';
import './styles/Opinion.scss';

const emojisClass = ['😧', '😕', '😐', '🙂', '😀'];

function Opinion({name, text, puntuacion}) {
    const emoji = emojisClass[puntuacion-1];
        return (
            <div className="Opinion cajita">
                <div className="Opinion_persona">
                    <span className="Opinion_persona-emoji">{emoji}</span>
                    <div className="Opinion_puntuacion">
                        <i className="icon-love"></i>
                        <span>{puntuacion}</span>
                    </div>
                    <h6> {name}</h6>
                    <span className="Opinion_persona-fecha">01/08/2020</span>
                </div>
                <p>{text}</p>
            </div>
    )
}

export default Opinion;