import React from 'react';
import Opinion from './Opinion';
import FormOpinionModal from './FormOpinionModal';
import DataComentarios from '../dataJSON/comentarios.json';

const COMENTARIOS = DataComentarios.comentariosJSON;
    


export default class Opiniones extends React.Component {
    state = {
        modalIsOpen: false,
    }
    handleModal = () => {
        if(this.state.modalIsOpen) {
            this.setState({modalIsOpen: false})
        } else {
            this.setState({modalIsOpen: true})
        }
    }
    render() {
        return (
            <div id="Opiniones">
                <div className="container">
                    <h2 className="sub-titulo">Opiniones</h2>
                    <div className="row cajita">
                        <h4>Puedes dejarnos tu opinion o comentario aquí...</h4>
                        <button className="btn btn-primary" onClick={this.handleModal}>Comentar</button>
                    </div>
                    <div className="row gridCols">
                        {COMENTARIOS.map((item, index) => 
                            <Opinion key={index} name={item.name} puntuacion={item.puntuacion} text={item.text} />
                        )}
                    </div>
                    <FormOpinionModal modalIsOpen={this.state.modalIsOpen} onClose={this.handleModal} />
                </div>
            </div>
        )
    }
}