import React from 'react';
import './styles/Grado.scss';
import {Link} from 'react-router-dom';

function Grado(props) {
    const {title, text, id, cover} = props.data;
    return (
        <Link className="Grado" to={`/grado/${id}`}>
            <figure className="Grado_image_Container">
              <img className="Grado_image" src={cover} alt={title}/>
            </figure>
            <div className="Grado_details">
              <h3 className="Grado_title">{title}</h3>
              <p className="Grado_description">{text}</p>
              <button className="Grado_url">Ver más</button>
            </div>
        </Link>
    )
}

export default Grado;