import React from 'react';
import { Link } from 'react-router-dom';
import '../components/styles/NotFound.scss';

export default class NotFound extends React.Component {
    render() {
        return (
            <React.Fragment>
                <div className="NotFound_text cajita">
                    <h1>404!</h1>
                    <h2>Estas perdid@... Regresa.</h2>
                </div>
                <figure className="cajita">
                    <img className="NotFound_image_cover" src="https://img.vixdata.io/pd/webp-large/es/sites/default/files/m/mujer-inteligente-estudiosa.jpg" alt="pageNotFound"/>
                </figure>
                <Link to="/" className="NotFound_return">
                    <button className="btn btn-primary">Home</button>
                </Link>
            </React.Fragment>
        );
    }
}