import React from 'react';
import GradoPage from '../components/GradoPage';
import dataGrades from '../dataJSON/grados.json';
import dataQuestions from '../dataJSON/preguntas.json';

export default class GradoPageContainer extends React.Component {
    state = {
        data: null,
        preguntas: null,
        grID: undefined,
        puntaje: 0,
        corregido: false
    }
    componentDidMount() {
        this.fetchData();
    }
    fetchData = () => {
        const gradoID = this.props.match.params.grID;
        const numID = this.numberGrade(gradoID)
        const dataGrade = dataGrades.gradosJSON[numID];
        const dataQuest = dataQuestions.preguntas[gradoID];
        this.setState({data: dataGrade, preguntas: dataQuest, grID: gradoID})
    }
    numberGrade = gradoID => {
        switch (gradoID) {
            case 'primero':
                return 0;
            case 'segundo':
                return 1;
            case 'tercero':
                return 2;
            case 'pre':
                return 3;
            default:
                return 4;
        }
    }
    subirPuntaje = (isCorrected, isOK) => {
        if(isCorrected && isOK) {
            this.state.puntaje++
        }
        this.setState({puntaje: this.state.puntaje})
    }
    corregir = () => {
        this.setState({corregido: true})
    }
    render() {
        const data = this.state.data;
        const preguntas = this.state.preguntas;
        if(data == null) {
            return "Lo sentimos, no Info"
        }
        return (
            <GradoPage data={data} preguntas={preguntas} grID={this.state.grID}
                        isCorrected={this.state.corregido} upScore={this.subirPuntaje}
                        puntaje={this.state.puntaje} corregir={this.corregir}/>
        )
    }
}